import { classNames } from '@/utils';
import { forwardRef } from 'react';
import { BadgeProps, BadgeWrapperProps } from './Badge.types';

/**
 * KRDS Badge 컴포넌트
 * @param variant 배지 종류 ('outline' | 'filled' | 'light')
 * @param color 배지 색상 ('primary' | 'secondary' | 'gray' | 'point' | 'danger' | 'warning' | 'success' | 'information' | 'disabled')
 * @param size 배지 크기 ('small' | 'medium' | 'large')
 * @param rounded 배지 모양 원형 여부
 * @param children 배지 내용
 * @param className 추가 커스텀 클래스
 */
export const Badge = forwardRef<HTMLSpanElement, BadgeProps>(
  (
    {
      variant = 'outline',
      color = 'primary',
      size = 'medium',
      rounded = false,
      children,
      className,
      ...props
    },
    ref
  ) => {
    const variantColorClass =
      variant === 'outline'
        ? `outline-${color}`
        : variant === 'filled'
          ? `bg-${color}`
          : `bg-light-${color}`;

    return (
      <span
        ref={ref}
        className={classNames(
          'krds-badge',
          size,
          variantColorClass,
          rounded && 'number',
          className
        )}
        {...props}
      >
        {children}
      </span>
    );
  }
);

Badge.displayName = 'Badge';

/**
 * KRDS Badge Wrapper 컴포넌트
 * @param children 배지 컴포넌트들
 * @param className 추가 커스텀 클래스
 */
export const BadgeWrapper = forwardRef<HTMLDivElement, BadgeWrapperProps>(
  ({ children, className, ...props }, ref) => {
    return (
      <div ref={ref} className={classNames('krds-badge-wrap', className)} {...props}>
        {children}
      </div>
    );
  }
);

BadgeWrapper.displayName = 'BadgeWrapper';
